/* Registeration Form Section
--------------------------------------------------------------------------------------*/
fieldset {
    border: none;
    display: grid;
    grid-template-columns: repeat(2, 20em);
    grid-template-rows: (10, auto);
    grid-row-gap: 1.5em;
    justify-content: center;

    hgroup {
        grid-column: 1/3;
        grid-row: 1/2;

        h3 {
            text-align: center;
        }
    }

    span:nth-of-type(1) {
        grid-column: 1/2;
        grid-row: 2/3;
    }

    span:nth-of-type(2) {
        grid-column: 1/2;
        grid-row: 3/4;
    }

    span:nth-of-type(3) {
        grid-column: 1/2;
        grid-row: 4/5;
    }

    span:nth-of-type(4) {
        grid-column: 1/2;
        grid-row: 5/6;
    }

    span:nth-of-type(5) {
        grid-column: 1/2;
        grid-row: 6/7;
    }

    span:nth-of-type(6) {
        grid-column: 1/2;
        grid-row: 7/8;
    }

    span:nth-of-type(7) {
        grid-column: 2/3;
        grid-row: 7/8;
    }

    span:nth-of-type(8) {
        grid-column: 1/2;
        grid-row: 8/9;
    }

    span:nth-of-type(9) {
        grid-column: 2/3;
        grid-row: 8/9;
    }

    span:nth-of-type(10) {
        grid-column: 1/3;
        grid-row: 10/11;
        margin: 0 auto;

        button {
            width: 300px;
            padding: 5px;
            border: none;
            font-size: 1em;
            background-color: gray;

            position: relative;
            z-index: 1;
            perspective: 300rem;
            overflow: hidden;
        }

        button:before {
            content: "";
            position: absolute;
            z-index: -1;
            width: 100%;
            height: 27px;
            top: 0;
            left: 0;
            background-color: hsla(0, 0%, 0%, 0.3);
            
            transform: translate3d(0px, -30px, 0px);
            transform-origin: top;

            transition-property: transform;
            transition-duration: 0.3s;
            transition-delay: 0s;
            transition-timing-function: ease-out;
        }
        
        button:hover,
        button:focus {
            color: #fff;
        }

        button:hover:before,
        button:focus:before {
            transform: translate3d(0px, 0px, 0px);
        }

    }

    .input-group {
        position: relative;
        perspective: 300rem;

        input {
            border: none;
            outline: none;
            border-bottom: 1px solid #000;
            background-color: transparent;
        }

        label {
            position: absolute;
            top: 0;
            left: 0;
            font-size: 0.8em;

            transform: translate3d(0px, 0px, 0px);
            transform-origin: top;

            transition-property: transform, font-size;
            transition-duration: 0.5s, 0.3s;
            transition-delay: 0s, 0s;
            transition-timing-function: ease-out;
        }

        input:focus + label,
        input:valid + label {
            transform: translate3d(0px, -18px, 0px);
            font-size: 0.6em;
        }
    }
}

/* Media Query Section
--------------------------------------------------------------------------------------*/
@include breakpoints(min-width, 25em) {
    fieldset {
        grid-template-columns: 20em;
        grid-template-rows: repeat(11, auto);

        hgroup {
            grid-column: 1/2;
            grid-row: 1/2;
        }
    
        span:nth-of-type(1) {
            grid-column: 1/2;
            grid-row: 2/3;
        }
    
        span:nth-of-type(2) {
            grid-column: 1/2;
            grid-row: 3/4;
        }
    
        span:nth-of-type(3) {
            grid-column: 1/2;
            grid-row: 4/5;
        }
    
        span:nth-of-type(4) {
            grid-column: 1/2;
            grid-row: 5/6;
        }
    
        span:nth-of-type(5) {
            grid-column: 1/2;
            grid-row: 6/7;
        }
    
        span:nth-of-type(6) {
            grid-column: 1/2;
            grid-row: 7/8;
        }
    
        span:nth-of-type(7) {
            grid-column: 1/2;
            grid-row: 8/9;
        }
    
        span:nth-of-type(8) {
            grid-column: 1/2;
            grid-row: 9/10;
        }
    
        span:nth-of-type(9) {
            grid-column: 1/2;
            grid-row: 10/11;
        }
    
        span:nth-of-type(10) {
            grid-column: 1/2;
            grid-row: 11/12;
        }
    }
}

@include breakpoints(min-width, 45em) {
    fieldset {
        grid-template-columns: repeat(2, 20em);
        grid-template-rows: repeat(9, auto);

        hgroup {
            grid-column: 1/3;
            grid-row: 1/2;
        }
    
        span:nth-of-type(1) {
            grid-column: 1/2;
            grid-row: 2/3;
        }
    
        span:nth-of-type(2) {
            grid-column: 1/2;
            grid-row: 3/4;
        }
    
        span:nth-of-type(3) {
            grid-column: 1/2;
            grid-row: 4/5;
        }
    
        span:nth-of-type(4) {
            grid-column: 1/2;
            grid-row: 5/6;
        }
    
        span:nth-of-type(5) {
            grid-column: 1/2;
            grid-row: 6/7;
        }
    
        span:nth-of-type(6) {
            grid-column: 1/2;
            grid-row: 7/8;
        }
    
        span:nth-of-type(7) {
            grid-column: 2/3;
            grid-row: 7/8;
        }
    
        span:nth-of-type(8) {
            grid-column: 1/2;
            grid-row: 8/9;
        }
    
        span:nth-of-type(9) {
            grid-column: 2/3;
            grid-row: 8/9;
        }
    
        span:nth-of-type(10) {
            grid-column: 1/3;
            grid-row: 9/10;
            
            button {
                display: block;
                margin: 0 auto;
            }
        }
    }
}